.DiceRollContainer {
    padding: 1em 1em;
    background-color: rgba(194, 194, 194, 0.6);
    border-radius: 15px;
    width: fit-content;
    height: fit-content;
}

.DiceContainer {
    display: flex;
    justify-content: space-between;
}

.DiceContainer-roll-btn-container {
    margin: .5em;
}

.DiceContainer-roll-btn {
    cursor: pointer;
    font-size: 1.2rem;
    height: 2.5rem;
    border: none;
    border-radius: 10px;
    color: white;
    background: rgb(38, 38, 43);
    outline: none;
    padding: 0.5rem 1.8rem;
    box-shadow: 0 0.3rem rgba(56, 46, 68, 0.65);
}

.DiceContainer-roll-btn:hover {
    filter: brightness(150%);
}

.DiceContainer-roll-btn:active {
    transform: translate(0, 0.3rem);
    box-shadow: 0 0.1rem rgba(255, 255, 255, .65);
}

.container-wobble {
    animation-name: container-wobble;
    animation-duration: 1s;
}

@keyframes container-wobble {
    from {
      transform: translate3d(0, 0, 0);
    }
  
    15% {
      transform: rotate3d(0, 0, 1, -20deg);
    }
  
    30% {
      transform: rotate3d(0, 0, 1, 47deg);
    }
  
    45% {
      transform: rotate3d(0, 0, 1, -40deg);
    }
  
    60% {
      transform: rotate3d(0, 0, 1, 50deg);
    }
  
    75% {
      transform: rotate3d(0, 0, 1, -35deg);
    }
  
    to {
      transform: translate3d(0, 0, 0);
    }
  }