.Dice {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    width: 100px;
    margin: 10px;
    color: white;
    font-size: 2rem;
    border-radius: 25px;
}

.red {
    background-color: rgb(155, 51, 51)
}

.black {
    background-color: rgb(43, 38, 49)
}

.wobble {
    animation-name: wobble;
    animation-duration: 1s;
}

@keyframes wobble {
    from {
      transform: translate3d(0, 0, 0);
    }
  
    15% {
      transform: translate3d(-50%, 0, 0) rotate3d(0, 0, 1, -50deg);
    }
  
    30% {
      transform: translate3d(50%, 0, 0) rotate3d(0, 0, 1, -180deg);
    }
  
    45% {
      transform: translate3d(-20%, 0, 0) rotate3d(0, 0, 1, 90deg);
    }
  
    60% {
      transform: translate3d(60%, 0, 0) rotate3d(0, 0, 1, 180deg);
    }
  
    75% {
      transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -5deg);
    }
  
    to {
      transform: translate3d(0, 0, 0);
    }
  }